body {
  box-sizing: border-box;
  text-align: center;
  padding: 0;
  margin: 0;
  font-size: 14px;
  position: relative;
}

body > header {
  position: sticky;
  top: 0;
  z-index: 4;
  box-shadow: 0px 0px 5px rgb(0, 42, 88);
}

#search-form {
  background-color: rgb(41, 124, 213);
  padding-top: 10px;
  padding-bottom: 10px;
}

#search-form > input[type='text'] {
  position: relative;
  padding: 0;
  height: 35px;
  width: 335px;
  border: none;
  border-radius: 3px;
  padding-left: 5px;
  outline: none;
  cursor: pointer;
  transition: 250ms;
}
#search-form > input[type='text']:hover,
#search-form > input[type='text']:focus {
  box-shadow: 0px 0px 10px rgb(0, 42, 88);
}

#search-form > button {
  outline: none;
  padding: 0;
  position: absolute;
  margin-left: -35px;
  border: none;
  height: 35px;
  width: 35px;
  border-top-right-radius: 3px;
  border-bottom-right-radius: 3px;
  border-top-left-radius: 3px;
  border-bottom-left-radius: 3px;
  background-color: rgb(255, 255, 255);
  transition: 250ms;
}

#search-form > button {
  margin-left: 10px;
  transition: 250ms;
}

#search-form > button:hover,
#search-form > button:focus {
  cursor: pointer;
  background-color: rgb(255, 255, 255);
  box-shadow: 0px 0px 10px rgb(0, 42, 88);
}

#search-form > button > img {
  margin-bottom: -1px;
}

body > div:nth-child(2) > div {
  margin: 0;
  box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.12), 0px 1px 1px rgba(0, 0, 0, 0.14),
    0px 2px 1px rgba(0, 0, 0, 0.2);
  border-radius: 4px;
  max-height: 350px;
  transition: 250ms;
}

body > div:nth-child(2) > div:hover {
  box-shadow: 0px 0px 5px rgb(0, 42, 88);
  scale: 1.01;
}

.info {
  display: flex;
  background-color: white;
  position: absolute;
  z-index: 1;
  justify-content: space-around;
  bottom: 0;
  width: 100%;
}

.gallery__image {
  width: 100%;
  height: 100%;
  position: relative;
  object-fit: cover;
}

.info-item {
  margin: 5px;
}
.gallery__link {
  display: block;
  height: 100%;
}

.gallery {
  display: grid;
  margin: 15px;
  grid-template-columns: repeat(4, 1fr);
  gap: 15px;
}

.button-hidden {
  visibility: hidden;
  margin-bottom: 15px;
  position: sticky;
  bottom: 10px;
  z-index: 3;
  padding: 7px 30px;
  background-color: rgb(41, 124, 213);
  border: none;
  color: white;
  border-radius: 4px;
  transition: 250ms;
}

.button-hidden:hover {
  background-color: white;
  outline: 1px solid rgb(41, 124, 213);
  cursor: pointer;
  color: rgb(41, 124, 213);
  box-shadow: 0px 0px 10px rgb(0, 42, 88);
}

body > div > div > a {
  display: block;
  height: 100%;
}
