body {
  box-sizing: border-box;
  text-align: center;
  margin: 0;
  padding: 0;
  font-size: 14px;
  position: relative;
}

body > header {
  z-index: 4;
  position: sticky;
  top: 0;
  box-shadow: 0 0 5px #002a58;
}

#search-form {
  background-color: #297cd5;
  padding-top: 10px;
  padding-bottom: 10px;
}

#search-form > input[type="text"] {
  height: 35px;
  width: 335px;
  cursor: pointer;
  border: none;
  border-radius: 3px;
  outline: none;
  padding: 0 0 0 5px;
  transition: all .25s;
  position: relative;
}

#search-form > input[type="text"]:hover, #search-form > input[type="text"]:focus {
  box-shadow: 0 0 10px #002a58;
}

#search-form > button {
  height: 35px;
  width: 35px;
  background-color: #fff;
  border: none;
  border-radius: 3px;
  outline: none;
  margin-left: 10px;
  padding: 0;
  transition: all .25s;
  position: absolute;
}

#search-form > button:hover, #search-form > button:focus {
  cursor: pointer;
  background-color: #fff;
  box-shadow: 0 0 10px #002a58;
}

#search-form > button > img {
  margin-bottom: -1px;
}

body > div:nth-child(2) > div {
  max-height: 350px;
  border-radius: 4px;
  margin: 0;
  transition: all .25s;
  box-shadow: 0 1px 3px #0000001f, 0 1px 1px #00000024, 0 2px 1px #0003;
}

body > div:nth-child(2) > div:hover {
  scale: 1.01;
  box-shadow: 0 0 5px #002a58;
}

.info {
  z-index: 1;
  width: 100%;
  background-color: #fff;
  justify-content: space-around;
  display: flex;
  position: absolute;
  bottom: 0;
}

.gallery__image {
  width: 100%;
  height: 100%;
  object-fit: cover;
  position: relative;
}

.info-item {
  margin: 5px;
}

.gallery__link {
  height: 100%;
  display: block;
}

.gallery {
  grid-template-columns: repeat(4, 1fr);
  gap: 15px;
  margin: 15px;
  display: grid;
}

.button-hidden {
  visibility: hidden;
  z-index: 3;
  color: #fff;
  background-color: #297cd5;
  border: none;
  border-radius: 4px;
  margin-bottom: 15px;
  padding: 7px 30px;
  transition: all .25s;
  position: sticky;
  bottom: 10px;
}

.button-hidden:hover {
  cursor: pointer;
  color: #297cd5;
  background-color: #fff;
  outline: 1px solid #297cd5;
  box-shadow: 0 0 10px #002a58;
}

body > div > div > a {
  height: 100%;
  display: block;
}

